    /**
     * 
     * @param {*} biaoti 标题
     * @param {*} msg 内容
     * @param {Laya.Handler} success_Hander 成功回调 
     * @param {Laya.Handler} cancel_Hander 失败/取消 回调
     */
    JiaoHukuang(biaoti, msg, success_Hander, cancel_Hander) {
        if (wx) {
            wx.showModal({
                title: biaoti,
                content: msg,
                showCancel: true, //隐藏取消
                success(res) {
                    if (res.confirm) { //点了确定
                        if (success_Hander) {
                            success_Hander.run();
                        }

                    } else if (res.cancel) { //点了取消
                        if (cancel_Hander) {
                            cancel_Hander.run();
                        }
                    } else if (res.fail) { //其他错误
                        if (cancel_Hander) {
                            cancel_Hander.run();
                        }
                    }
                }
            })
        }
    }

