    //加载所有的武器信息
    LoadEquip() {
        for (let index = 0; index < 28; index++) {
            var equip = {
                id: index,//武器ID
                texture: "",//皮肤路径
                grade: index + 1,//武器等级
                sellPrice: (index + 1) * 100,//出售价格
                buyPrice: (index + 1) * 200,//购买价格
                Attack: (index + 1) * 20,//攻击力
            }
            window.arsenal.push(equip);
        }
    }
